//*  ------------------------ [Greed Card a] -----------------------------------
.card-product {
	display: flex;
	height: 100%;
	flex-direction: column;

	// .card__thumb
	&__thumb {
		flex: 1 1 auto;
	}

	// .card__image
	&__image {
		height: 100%;
		padding-bottom: 10px;

		a {
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: flex-end;
		}

		img {
			vertical-align: bottom;
		}
	}

	// .card__content
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	// .card__text
	&__text {
		h2 {
			white-space: nowrap;
			color: rgb(88, 88, 86);
			font-size: 2rem;
			font-weight: 700;
			line-height: 120%; /* 24px/20 */
			letter-spacing: 0%;
			text-align: center;
			margin-bottom: 10px;
		}
	}

	// .card__button
	&__button {
		cursor: pointer;
		width: 166px;
		padding: 10px 50px;
		border: 1px solid rgb(88, 88, 86);
		border-radius: 10px;
		transition:
			background-color 0.3s,
			color 0.3s ease-in-out;

		&:hover {
			background-color: $color;
			border: 1px solid $color;
			span {
				color: $bg-color;
			}
		}

		span {
			color: rgb(0, 0, 0);
			font-size: 1.4rem;
			line-height: 121.428571%; /* 17px/14 */
			letter-spacing: 0%;
			text-align: center;
		}
	}
}
// -----------------------------------------------------------------------------
.company-info {
	// .company-info__title
	&__title {
		width: 100%;
		margin-bottom: 6rem;
		color: $color;
		font-size: 4rem;
		font-weight: 700;
		line-height: 120%; /* 48px/40 */

		@include _375 {
			text-align: center;
		}
	}

	// .company-info__content
	&__content {
		height: 100%;

		@include _768 {
			display: flex;
			flex-direction: column;
			width: auto;
			align-items: center;
		}
	}

	// .discounts-info__list
	&__list {
		@include _768 {
			font-weight: 600;
		}
	}

	// .company-info__item
	&__item {
		display: flex;
		align-items: center;

		._icon {
			color: $color;
			font-size: 10px;
			margin-right: 8px;
		}

		&:not(:last-child) {
			margin-bottom: 8px;
		}
		span {
			font-size: 13px;
		}
	}
}

// -----------------------------------------------------------------------------
.button {
	border: 1px solid #585856;
	border-radius: 10px;
	white-space: nowrap;
	transition:
		background-color 0.3s,
		color 0.3s ease-in-out;
	font-size: 1.4rem;
	height: 38px;
	min-width: 16.5rem;

	&:active {
		background-color: $color;
		color: $bg-color;
	}

	&.button--red {
		color: $bg-color;
		background: $color;
		border: none;

		&:active {
			background-color: $bg-color;
			color: black;
		}
	}
}
// -----------------------------------------------------------------------------
.login-container {
	// .login-container__body
	&__body {
		position: relative;
		margin-right: 2rem;
		width: 100%;
		height: 100%;
		z-index: 10;
	}

	// .login-container__login
	&__login {
		display: flex;
		align-items: center;
		justify-content: flex-end;

		[class*='icon-'] {
			font-size: 18px;
		}
	}

	// .login-container__list
	&__list {
		cursor: pointer;
		width: auto;
		display: flex;
		justify-content: space-between;

		@include _screen-960 {
			padding: 2rem;
			pointer-events: none;
			position: absolute;
			top: 0;
			right: 0;
			opacity: 0;
			transform: translateY(60%);
			border: 1px solid $color;
			background: $bg-color;
			border-radius: 10px;
			transition: all 0.5s ease-in-out;
			&._visible {
				pointer-events: all;
				opacity: 1;
				display: flex;
			}
		}

		span {
			margin-right: 0.3rem;
			line-height: 170%;
		}
	}

	// .login-container__link
	&__link {
		white-space: nowrap;
		font-size: 14px;
		transition: color 0.3s ease;

		&:hover {
			color: $color;
			text-decoration: underline;
		}
		&:not(:last-child) {
			margin-right: 0.3rem;
		}

		@include _min-960 {
			&:first-child {
				padding-left: 0.5rem;
			}
			line-height: 0;
		}

		@include flex-align-center;
	}

	// .login-container__button
	&__user-button {
		&._icon {
			pointer-events: none;
		}
	}

	// .login-container__phone
	&__phone {
		white-space: nowrap;
		justify-self: end;
		align-self: center;

		a {
			line-height: 120%;
			&:hover {
				line-height: 110%;
				font-size: 1.455rem;
				letter-spacing: -0.4px;
			}
		}
	}

	// .login-container__button//
	&__button {
		height: 38px;
		@include _690 {
			display: flex;
			align-items: center;
		}
	}
}

// -----------------------------------------------------------------------------
.swiper-wrapper {
	align-items: flex-start;
}
// -----------------------------------------------------------------------------

// -----------------------------------------------------------------------------
._ibg {
	position: relative;
	// height: 100%;
	// width: 100%;
	img {
		position: absolute;
		top: 0;
		right: 0;
		// width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
// -----------------------------------------------------------------------------
._lnb {
	position: relative;
	&::before {
		display: block;
		content: attr(title);
		font-weight: 700;
		height: 0;
		overflow: hidden;
		visibility: hidden;
	}
}
// -----------------------------------------------------------------------------
._clip-circle {
	@include _min-920 {
		position: relative;
		z-index: 2;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			color: #fff;
			background-color: $color;
			clip-path: circle(var(--size) at var(--pos));
			transition: all 0.5s ease-in-out;
			z-index: 1;
			border-radius: 32px;
		}

		&:hover {
			a {
				color: $bg-color;
			}
			&:before {
				--size: 235px;
			}
		}
	}
}
// -----------------------------------------------------------------------------
.checkbox {
	&._error {
		.checkbox__lable::before {
			box-shadow: 0px 0px 14px 3px #f16666;
		}
	}

	// .checkbox__input
	&__input {
		display: none;
		&:checked + .checkbox__label .icon-arow-down {
			display: block;
			scale: 1;
			opacity: 1;
		}
	}

	&__checed {
		cursor: pointer;
		position: relative;
		display: flex;
		align-items: center;
		font-size: 16px;
		line-height: 140%;
	}

	// .checkbox__lable
	&__label {
		max-width: 250px;
		cursor: pointer;
		position: relative;
		display: inline-flex;
		align-items: center;
		font-size: 16px;
		line-height: 110%;

		.icon {
			margin-right: 0.5rem;
			margin-left: -3.2rem;
			height: 3.5rem;
			@include _1024 {
				margin-left: -3.6rem;
			}
		}

		.icon-arow-down {
			position: absolute;
			position: relative;
			display: flex;
			align-items: center;
			transition: all 0.3s ease-in-out;
			opacity: 0;
			height: 28px;
			font-size: 2.8rem;
			color: #2cc55f;
		}

		&::before {
			content: '';
			position: relative;
			margin-right: 10px;
			flex: 0 0 20px;
			height: 20px;
			align-self: center;
			border: 1px solid $color;
			border-radius: 7px;
			transition: box-shadow 0.3s ease-in-out;
		}

		a {
			text-decoration: underline;
			font-weight: 600;
			&:hover {
				text-decoration: none;
			}
		}
		span {
			font-size: 10px;
		}
	}
}
.product-offer {
	&__title--favourites {
		display: flex;
		justify-content: center;
	}
}
